.profile{
    display: flex;
    flex-direction: row;
    margin: 41px auto 51px;
    max-width: 880px;
    justify-content: space-between;
    align-items: center;
}
@media screen and (max-width: 900px){
    .profile {
       max-width: 600px;
       flex-direction: column;
       margin: 41px auto 36px;
    }
}